// mixin
@mixin round($wh:45px,$color:rgba(0,0,0,0),$top:0,$left:0) {
  width: $wh;
  height: $wh;
  border-radius: 50%;
  background: $color;
  top:$top;
  left:$left;
  z-index:-1;
}

html {
  -ms-touch-action: none;  /* 阻止windows Phone 的默认触摸事件 */
  overflow: hidden;
}
#pageContain {
  overflow: hidden;
  .page {
    display: none;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    .contain {
      width: 100%;
      height: 100%;
      display: none;
      //position: relative;
      z-index: 0;
    }
    .po-rela{
      position: relative;
    }
    .po-abso{
      position: absolute;
    }
  }
  .current .contain,.slide .contain {
    display: block;
  }
  .current {
    display: block;
    z-index: 1;
  }
  .slide {
    display: block;
    z-index: 2;
  }
  .swipe {
    display: block;
    z-index: 3;
    transition-duration: 0ms !important;
    -webkit-transition-duration: 0ms !important;
  }

  /*-------------------------------- page 公共元素 ------------------------------------*/
  .title {
    -webkit-animation:dragTop .7s ease-in-out both;
    -moz-animation:dragTop .7s ease-in-out both;
    -ms-animation:dragTop .7s ease-in-out both;
    animation:dragTop .7s ease-in-out both;
  }
  .text {
    -webkit-animation:dragTop .7s ease-in-out .3s both;
    -moz-animation:dragTop .7s ease-in-out .3s both;
    -ms-animation:dragTop .7s ease-in-out .3s both;
    animation:dragTop .7s ease-in-out .3s both;
  }

